.unpadded-col {
  padding-right: 5px;
  padding-left: 5px;
}

.img-responsive-ht {
  width:auto;
  max-width: 100%;
  max-height: calc(100vh - 100px);
}

.modal-dialog {
  position: relative;
  display: table; /* This is important */ 
  overflow-y: auto;    
  overflow-x: auto;
  width: auto;
  min-width: 300px;   
}

.cover-icon {
  object-fit: cover;
  margin-bottom: 0px;
  margin-right: 5px;
}

.fixed-card-img {
  object-fit: cover;
  object-position: 50% 0%;
  height: calc(33vh);
  max-width: 100%;
}

.draggable-img {
  width: 100%; /* or any custom size */
  height: 100%; 
  object-fit: contain;
}

@media(min-width: 1440px) {
  .container {
    max-width: 1400px;
  }
}

@media(min-width: 1440px) {
  .modal-lg, .modal-xl {
    max-width: 1200px;
  }
}